div.timeline-time {
  float: left;
  padding-top: 10px;
}

div.timeline-time:before {
  padding: 3px;
  content: "~";
}

div.timeline-time:after {
  padding: 3px;
  content: "~";
}

div.timeline-total {
  border-top: 1px solid #999;
}

#timelinecontainer {
  border: 1px solid #c6d1ad;
  border-top: 0px;
  position: relative;
}

#timelinecontainer:before,
#timelinecontainer:after {
  content: "";
  display: table;
}

#timelinecontainer:after {
  clear: both;
}

#timelinecontainer {
  zoom: 1;
}

#timeline {
  overflow: hidden;
  position: absolute;
  left: 265px;
  right: 225px;
  border-left: 1px solid #c6d1ad;
  border-right: 1px solid #c6d1ad;
}

#timeline-events {
  width: 10000px;
}

/* Allow the timeline to scroll all the way to the end with a little margin */
#timeline-events:after,
#timeline-events:before {
  content: "";
  display: block;
  width: 1px;
  height: 1px;
  float: left;
}

#timeline p {
  margin: 0;
}

