/* Faux table styles that allow revealing data row by row in hints */

.fake_header > span {
  font-weight: bold;
  display: inline-block;
  padding-left: 10px;
  border-bottom: 2px solid #cccccc;
}

.fake_row > span {
  display: inline-block;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  border-bottom: 1px solid #ddd;
}

.fake_row:nth-child(n) > span {
  border-bottom: none;
}

.fake_row:nth-child(2n + 1) > span {
  background-color: #f3f3f3;
}

#timelinecontainer .simple-button {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
  white-space: nowrap;
}

.thumbnail a {
  outline: none;
  color: #fff;
}

.thumbnail a:hover {
  text-decoration: none;
}

.thumbnail div.thumb {
  background-size: 200px 150px;
  background-position: no-repeat top left;
  background-position: 0px -25px;
  background-position-y: -80px\9;
  background-position-x: -100px\9;
  width: 200px;
  height: 100px;
  border: 1px solid #aaa;
  -webkit-box-shadow: 0 0 3px #ccc;
  -moz-box-shadow: 0 0 3px #ccc;
  box-shadow: 0 0 3px #ccc;
  margin-left: 14px; /* to line up with other titles */
  margin-bottom: 8px;
  margin-top: 4px;
}

.thumbnail div.thumbnail_label {
  padding: 5px 10px;
  max-width: 180px;
  margin: 0;
  text-align: left;
  margin-top: 68px;
  background: #333;
  background-color: rgba(30, 30, 30, 0.9);
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
}

.thumbnail div.thumbnail_desc {
  width: 180px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.thumbnail div.thumbnail_teaser {
  height: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  text-decoration: none;
  font-size: 11px;
}

.sortable {
  background: #eee;
  border: 1px solid #ccc;
  border-bottom: 1px solid #aaa;
  padding: 13px;
  position: relative;
  z-index: 2;

  box-shadow: 0 1px 2px #ccc;
  -moz-box-shadow: 0 1px 2px #ccc;
  -webkit-box-shadow: 0 1px 2px #ccc;
}

.sortable > ul {
  list-style-type: none;
}

.sortable > ul > li {
  background-color: #fff;
  border: 1px solid #b9b9b9;
  border-bottom-color: #939393;
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  cursor: pointer;
  margin-right: 4px;
  min-width: 65px;
  height: 65px;
  text-align: center;
  font-size: 1.2em;
  float: left;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.sortable > ul > li.placeholder {
  background: #ddd;
  border: 0;
  border: 1px solid #ccc;
  float: left;
}

.sortable code {
  line-height: 65px;
}

.sortable > ul > li:hover {
  border-color: #ffa500;
  box-shadow: 0 0 4px #c56f00;
  -moz-box-shadow: 0 0 4px #c56f00;
  -ms-box-shadow: 0 0 4px #c56f00;
  -o-box-shadow: 0 0 4px #c56f00;
  -webkit-box-shadow: 0 0 4px #c56f00;
}

.sortable > ul > li:active,
.sortable > ul > li.dragging {
  background-color: #ffedcd;
  opacity: 0.8;
  filter: alpha(opacity = 80);
}

.box-whisker-sortable > .sortable {
  background: #f8f8f8;
  border: 0;
  border-bottom: 0;
  box-shadow: 0 0 0;
  -moz-box-shadow: 0 0 0;
  -webkit-box-shadow: 0 0 0;
  padding-right: 0;
}

.box-whisker-sortable > .sortable > ul > li {
  width: 28px;
  min-width: 28px;
  height: 28px;
  margin-right: 2px;
  font-size: 1em;
  border: 1px solid #ddd;
}
.box-whisker-sortable > .sortable code {
  line-height: 32px;
}

#problemarea div#congruent-triangles {
